
.vc-ripple-ink{display:block;overflow:hidden;border-radius:inherit;position:absolute;top:0;left:0;right:0;bottom:0;-webkit-mask-image:-webkit-radial-gradient(circle, #fff, #000)
}
.vc-ripple-ink .ripple{position:absolute;width:0;height:0;pointer-events:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;border-radius:50%;background-color:currentColor;background-clip:padding-box;transform:scale(0);transition:transform 0.8s ease-out, opacity 0.8s ease-out
}
.vc-ripple-ink .ripple.held{transform:scale(1)
}
.vc-ripple-ink .ripple.done{opacity:0 !important
}



